
#app .select2-container {
  width: 100% !important;
}
#app .select2-container .select2-selection--single {
  height: 34px;
  border: 1px solid #c2cad8;
  outline: none;
}
#app .select2-container .select2-selection--single .select2-selection__rendered {
  line-height: 34px;
}
#app .select2-container .select2-selection--single .select2-selection__arrow {
  height: 32px;
}
#app .cov-vue-date {
  width: 100% !important;
}
#app .cov-vue-date .cov-datepicker {
  width: 100% !important;
}
#app .checkbox-component,
#app .radio-component {
  line-height: 34px;
  margin-right: 20px;
}
#app .checkbox-component > input + label > .input-box,
#app .radio-component > input + label > .input-box {
  width: 20px;
  height: 20px;
  border: 1px solid #c2cad8;
  vertical-align: middle;
  -webkit-box-shadow: none;
          box-shadow: none;
  margin: 0 5px;
  cursor: pointer;
}
#app .checkbox-component > input + label > .input-box > .input-box-tick {
  position: relative;
  top: -3px;
  margin-top: -10px;
}
#app .checkbox-component > input + label > .input-box > .input-box-tick path {
  stroke: #3498db;
}
#app .radio-component > input + label > .input-box > .input-box-circle {
  cursor: pointer;
  background-color: #3498db;
  border-radius: 50%;
}
#app .multiselect .multiselect__select {
  height: 34px;
}
#app .multiselect .multiselect__tags {
  min-height: 34px;
  padding: 2px 40px 0 4px;
  border: 1px solid #c2cad8;
}
#app .multiselect .multiselect__tags .multiselect__input {
  margin-top: 4px;
  margin-bottom: 4px;
}
#app .multiselect .multiselect__tags .multiselect__tags-wrap .multiselect__tag {
  margin-top: 3px;
  margin-bottom: 0;
}
#app .input-inline {
  max-width: 100%;
}
#app .sweet-modal-overlay.theme-light {
  background: rgba(0,0,0,0.2);
}


